import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BasePage } from '../base/base.page';
import { Location } from '@angular/common';
import { UtilsService } from '../@core/utils.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { TranslateService } from '@ngx-translate/core';
import { Utils } from '../@global/utils';
import { Constants } from '../@global/constants';
import { IElement } from '../@store/global-info/global-info.model';
import {
  GetAcademicModalityInfo,
  GetAcademicTypeInfo,
  GetStudyAreaInfo,
} from '../@store/global-info/global-info.actions';
import { GlobalInfoState } from '../@store/global-info/global-info.state';
import { Subscription } from 'rxjs';
import { CreateAcademicProgram } from '../@store/academic-programs/academic-programs.actions';
import { InformService } from '../@core/inform.service';
import { Chooser, ChooserResult } from '@ionic-native/chooser/ngx';
import { SportsModalComponent } from '../@shared/components/sports-modal/sports-modal.component';
import { ModalController } from '@ionic/angular';
import { GetLanguageInfo } from '../@store/global-info/global-info.actions';
import { NavigationExtrasService } from '../@core/navigation-extras.service';

const MAX_YEARS_MARGIN_START = 5;
const MAX_YEARS_MARGIN_END = 10;

@Component({
  selector: 'app-academic-program',
  templateUrl: './academic-program.page.html',
  styleUrls: ['./academic-program.page.scss'],
})
export class AcademicProgramPage extends BasePage implements OnInit, OnDestroy {
  public formGroup: FormGroup;
  public customActionSheetStudyAreaOptions: any;
  public customActionSheetModalityOptions: any;
  public customActionSheetAcademicTypeOptions: any;
  public customActionSheetLanguageOptions: any;
  private locationData: { country: string; region: string; province: string; city: string; region_name: string };
  public hasLocation: boolean;
  public hasSport = false;
  public programLocation: string;
  public studyAreaList: IElement[];
  public modalityList: IElement[];
  public academicTypeList: IElement[];
  public languageList: IElement[];

  private studyAreaSubscription: Subscription;
  private modalitySubscription: Subscription;
  private academicTypeSubscription: Subscription;
  private languageInfoSubscription: Subscription;
  private profileId: string;
  public maxlength = Constants.MAX_PUBLICATION_DESCRIPTION_SIZE;

  public sportsList: IElement[] = [];

  private acceptedExtensions = ['pdf'];
  public brochure: ChooserResult;
  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    private formBuilder: FormBuilder,
    private translateService: TranslateService,
    private route: ActivatedRoute,
    private chooser: Chooser,
    private navigationExtrasService: NavigationExtrasService,
    private informService: InformService,
    public modalController: ModalController
  ) {
    super(location, router, store, utilsService);
  }

  public doIonViewWillEnter(): void {
    this.setLocationData();
    this.store.dispatch(new GetStudyAreaInfo());
    this.store.dispatch(new GetAcademicModalityInfo());
    this.store.dispatch(new GetAcademicTypeInfo());
    this.store.dispatch(new GetLanguageInfo());
  }

  ngOnInit() {
    this.subscribeStudyAreaInfo();
    this.subscribeModalityInfo();
    this.subscribeAcademicType();
    this.subscribeLanguageInfo();
    this.initForm();

    this.profileId = this.route.snapshot.params['profileId'];

    const studyAreaTitle = this.translateService.instant(`formFields.studyArea`);
    this.customActionSheetStudyAreaOptions = {
      header: studyAreaTitle,
    };
    const modalityTitle = this.translateService.instant(`formFields.modality`);
    this.customActionSheetModalityOptions = {
      header: modalityTitle,
    };

    const academicTypeTitle = this.translateService.instant(`formFields.academicType`);
    this.customActionSheetAcademicTypeOptions = {
      header: academicTypeTitle,
    };

    const languageTitle = this.translateService.instant(`formFields.language`);
    this.customActionSheetLanguageOptions = {
      header: languageTitle,
    };
  }

  private initForm(): void {
    this.formGroup = this.formBuilder.group({
      title: ['', [Validators.required]],
      description: ['', [Validators.required, Validators.maxLength(this.maxlength)]],
      study_area: ['', [Validators.required]],
      modality: ['', [Validators.required]],
      language: [''],
      // price: ['', Validators.pattern(Constants.ONLY_NUMBERS_PATTERN)],
      start_date: ['', [Validators.required]],
      end_date: [''],
      website: [''],
      brochure: [''],
      academic_type: ['', [Validators.required]],
      // other_costs: [''],
      check_email: [false],
      check_website: [false],
      contact_email: ['', [Validators.email, Validators.maxLength(Constants.MAX_LENGTH_USERNAME_COGNITO)]],
    });
  }

  public setOptionSelected(option: number) {
    if (option === 1) {
      if (this.formGroup.get('check_email').value) {
        // when select one, the other one is set to false
        this.formGroup.get('check_website').setValue(false);
        this.formGroup.get('contact_email').enable();
        this.formGroup.get('contact_email').setValidators([Validators.required, Validators.email]);
        this.formGroup.get('website').setValue('');
        this.formGroup.get('website').clearValidators();
        this.formGroup.get('website').disable();
      }
    } else {
      if (this.formGroup.get('check_website').value) {
        // when select one, the other one is set to false
        this.formGroup.get('check_email').setValue(false);
        this.formGroup.get('website').enable();
        this.formGroup.get('website').setValidators([Validators.required, Validators.pattern(Constants.URL_PATTERN)]);
        this.formGroup.get('contact_email').setValue('');
        this.formGroup.get('contact_email').clearValidators();
        this.formGroup.get('contact_email').disable();
      }
    }
    this.formGroup.get('website').updateValueAndValidity();
    this.formGroup.get('contact_email').updateValueAndValidity();
  }

  private setLocationData(): void {
    if (this.navigationExtrasService.getExtras() && this.navigationExtrasService.getExtras().location) {
      const location = this.navigationExtrasService.getExtras().location;

      this.hasLocation = !!location.country;

      if (this.hasLocation) {
        this.programLocation = Utils.generateUserFriendlyLocation(
          location.country,
          location.region,
          location.city,
          location.province,
          location.region_name
        );

        this.locationData = {
          country: location.country?.id,
          region: location.region?.id,
          province: location.province?.id,
          city: location.city,
          region_name: location.region_name,
        };
      }

      this.navigationExtrasService.setExtras(null);
    }
  }

  private subscribeStudyAreaInfo(): void {
    this.studyAreaSubscription = this.store.select(GlobalInfoState.getStudyArea).subscribe((data) => {
      this.studyAreaList = data;
    });
  }

  private subscribeModalityInfo(): void {
    this.modalitySubscription = this.store.select(GlobalInfoState.getAcademicModality).subscribe((data) => {
      this.modalityList = data;
    });
  }

  private subscribeAcademicType(): void {
    this.academicTypeSubscription = this.store.select(GlobalInfoState.getAcademicType).subscribe((data) => {
      this.academicTypeList = data;
    });
  }

  private subscribeLanguageInfo(): void {
    this.languageInfoSubscription = this.store.select(GlobalInfoState.getLanguage).subscribe((data) => {
      this.languageList = data;
    });
  }

  public getMinDate() {
    return Utils.getInitdate(70);
  }

  public getMaxStartDate() {
    return new Date().getFullYear() + MAX_YEARS_MARGIN_START;
  }

  public getMaxEndDate() {
    return (new Date(this.getStart()).getFullYear() + MAX_YEARS_MARGIN_END).toString();
  }

  public getStart() {
    return this.formGroup.get('start_date').value;
  }

  public getDateDisplayFormat(): string {
    return Constants.DATE_DISPLAY_MONTH_YEAR_FORMAT;
  }

  public getDatePickerFormat(): string {
    return Constants.DATE_PICKER_MONTH_YEAR_FORMAT;
  }

  public setEndDate() {
    const startDate = this.formGroup.get('start_date').value;
    const endDate = this.formGroup.get('end_date').value;

    console.log(this.getMaxEndDate());
    if (endDate < startDate || endDate > this.getMaxEndDate()) {
      this.formGroup.get('end_date').setValue('');
    }

    if (startDate) {
      this.formGroup.get('end_date').enable();
    } else {
      this.formGroup.get('end_date').disable();
    }

    this.formGroup.get('end_date').updateValueAndValidity();
  }

  public async openSportsModal() {
    const modal = await this.modalController.create({
      component: SportsModalComponent,
      componentProps: {
        sportsSelected: [...this.sportsList],
      },
    });

    modal.onDidDismiss().then((response) => {
      if (response.data) {
        this.sportsList = [];
        response.data.forEach((item: IElement) => {
          this.sportsList.push(item);
        });
        this.hasSport = this.sportsList.length > 0;
      }
    });

    return await modal.present();
  }

  public saveAcademicProgram(): void {
    if (this.utilsService.isValidForm(this.formGroup)) {
      if (!this.formGroup.get('check_email').value && !this.formGroup.get('check_website').value) {
        this.informService.showWarning('academicProgram.selectOption');
        return;
      }

      const input = { ...this.formGroup.value, ...this.locationData };

      if (input.check_email) {
        delete input.website;
      } else if (input.check_website) {
        delete input.contact_email;
      }

      delete input.check_email;
      delete input.check_website;

      if (input.price === '' || isNaN(input.price)) {
        delete input.price;
      }

      input.sports = [...this.sportsList.map((item: IElement) => item.id)];

      this.store.dispatch(new CreateAcademicProgram(this.profileId, this.brochure, input)).subscribe(
        () => {
          this.goBack();
        },
        (error) => {
          console.log('Error creating academic program');
          console.log(error);
        }
      );
    }
  }

  public async selectPDF() {
    try {
      this.brochure = await this.utilsService.selectPDF();
    } catch (error) {
      throw new Error(error);
    }
  }

  private disconnectObservers(): void {
    if (this.studyAreaSubscription) {
      this.studyAreaSubscription.unsubscribe();
    }

    if (this.modalitySubscription) {
      this.modalitySubscription.unsubscribe();
    }

    if (this.academicTypeSubscription) {
      this.academicTypeSubscription.unsubscribe();
    }

    if (this.languageInfoSubscription) {
      this.languageInfoSubscription.unsubscribe();
    }
  }

  ngOnDestroy(): void {
    this.disconnectObservers();
  }
}
